@import "../../global.scss";

.portfolio{
    background-color: #eeaa78;
    display: flex;
    flex-direction: column;
    align-items: center;

    h1{
        margin-top:10px;
        font-size: 50px;
        border: 50px black;
        padding: 50px;
        margin: 20px;

        @include mobile{
            font-size: 20px;
            border: none;
            padding: none;
            margin: none;;
        }
    }

    ul{
        margin: 10px;
        padding: 0;
        list-style: none;
        display: flex;

        @include mobile{
            flex-wrap: wrap;
            justify-content: center;
        }
    }

    .container{
        width: 70%;
        display: flex;
        align-items: center;
        justify-content: center;
        flex-wrap: wrap;

        @include mobile{
            width: 100%;
        }
        .item{
            width: 268px;
            height: 500px;
            border-radius: 20px;
            border: 1px solid lightgray;
            margin: 10px 20px;
            display: flex;
            align-items: center;
            justify-content: center;
            color: white;
            position: relative;
            transition: all .5s ease;
            cursor: pointer;

            @include mobile{
                margin: 10px 0;
                width: 130px;
                height: 100px;
            }

            h3{
                position: absolute;
                font-size: 20px;

            }

            img{
                width: 100%;
                height: 100%;
                object-fit: cover;
                z-index: 1;

            }

            &:hover{
                background-color: $mainColor;
                img{
                    opacity: 0.2;
                    z-index: 0;
                }
            }


        }
    }
}