@import "../../global.scss";

.intro{
    background-color: beige;
    display: flex;

    @include mobile{
        flex-direction: column;
        align-items: center;
    }
    
    .left{
        flex: 0.5;
        overflow: hidden;

        .imgContainer{
            margin-top: 50px;
            width: 800px;
            height: 800px;
            background-color: lightblue;
            border-radius: 50%;
            display: flex;
            align-items: flex-end;
            justify-content: center;
            float: right;

            @include mobile{
                align-items: flex-start;
            }

            img{
                height: 90%;
                // float: top;

                @include mobile{
                    height: 50%
                }
            }
        }
    };

    .right{
        flex: 0.5;
        position: relative;

        .wrapper{
            width: 100%;
            height: 100%;
            padding-left: 50px;
            display: flex;
            flex-direction: column;
            justify-content: center;

            @include mobile {
                padding-left: 0;
                align-items: center;
            }

            h1{
                font-size: 60px;
                margin: 10px 0;

                @include mobile{
                    font-size: 40px;
                }
            }

            h2{
                font-size: 35px;
            }

            h3{
                font-size: 30px;

                span{
                    font-size: inherit;
                    color: crimson;
                }

                .ityped-cursor{
                   animation: blink 3s infinite; 
                }
                
                @include mobile{
                    font-size: 20px;
                }

                @keyframes blink {
                    50%{
                        opacity: 1;
                    }
                    
                    100%{
                        opacity: 0;;
                    }
                    
                }
            }
        }
        a{
            position: absolute;
            bottom: 10px;
            right: 40%;

            img{
                width: 40px;
                animation: arrowBlink 2s infinite; 
            }

            @keyframes arrowBlink {
                100%{
                    opacity: 0;
                }
                
            }
        }
    }
}